import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'login',
      hidden:true,
      meta:{
        name:'主页',
        requireAuth: true,
      },
    },
    {
      path: '/login',
      name: 'Login',
      hidden:true,
      meta:{
        name:'登录'
      },
      component: ()=>import("../pages/Login/index.vue"),
    },
    {
      path: '/home',
      name: 'Home',
      redirect:"/store",
      meta:{
        name:'首页',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/store',
          name: 'Store',
          component: ()=>import("../pages/Home/store.vue"),
          meta:{
            name:'商城管理',
            requireAuth: true,
          },
        },{
          path: '/partner',
          name: 'Partner',
          component: ()=>import("../pages/Home/partner.vue"),
          meta:{
            name:'合作伙伴管理',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/swiper',
      name: 'Swiper',
      meta:{
        name:'轮播图',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/swiper',
          name: 'Swiper',
          component: ()=>import("../pages/Swiper/index.vue"),
          meta:{
            name:'轮播图管理',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/product',
      name: 'Product',
      meta:{
        name:'产品和方案',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/product',
          name: 'Product',
          component: ()=>import("../pages/Product/index.vue"),
          meta:{
            name:'添加解决方案',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/casepresent',
      name: 'CasePresent',
      meta:{
        name:'案例展示',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/case',
          name: 'Case',
          component: ()=>import("../pages/CasePresent/index.vue"),
          meta:{
            name:'案例管理',
            requireAuth: true,
          },
        },
        {
          path: '/category',
          name: 'Category',
          component: ()=>import("../pages/CasePresent/category.vue"),
          meta:{
            name:'分类管理',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/support',
      name: 'Support',
      meta:{
        name:'服务与支持',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/support',
          name: 'Support',
          component: ()=>import("../pages/Support/index.vue"),
          meta:{
            name:'常见问题',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/about',
      name: 'About',
      meta:{
        name:'关于我们',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/compan',
          name: 'Compan',
          component: ()=>import("../pages/About/CompanyEnvir.vue"),
          meta:{
            name:'公司环境',
            requireAuth: true,
          },
        },
        {
          path: '/leadMember',
          name: 'LeadMember',
          component: ()=>import("../pages/About/leadMember.vue"),
          meta:{
            name:'主要成员',
            requireAuth: true,
          },
        },{
          path: '/vitality',
          name: 'Vitality',
          component: ()=>import("../pages/About/vitality.vue"),
          meta:{
            name:'活力团队',
            requireAuth: true,
          },
        },{
          path: '/overseasTeam',
          name: 'OverseasTeam',
          component: ()=>import("../pages/About/OverseasTeam.vue"),
          meta:{
            name:'境外团队',
            requireAuth: true,
          },
        }
      ]
    },
    {
      path: '/thesea',
      name: 'TheSea',
      meta:{
        name:'那片海咖啡',
        requireAuth: true,
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/seaManger',
          name: 'SeaManger',
          component: ()=>import("../pages/TheSea/seaManger.vue"),
          meta:{
            name:'那片海咖啡管理',
            requireAuth: true,
          },
        },{
          path: '/businscope',
          name: 'BusinScope',
          component: ()=>import("../pages/TheSea/BusinScope.vue"),
          meta:{
            name:'经营范围管理',
            requireAuth: true,
          },
        },{
          path: '/coffeeenvir',
          name: 'CoffeeEnvir',
          component: ()=>import("../pages/TheSea/coffeeEnvir.vue"),
          meta:{
            name:'环境管理',
            requireAuth: true,
          },
        } 
      ]
    },
    {
      path: '/servicecenter',
      name: 'ServiceCenter',
      meta:{
        name:'服务中心'
      },
      component: ()=>import("../pages/Layout/index.vue"),
      children:[
        {
          path: '/reservat',
          name: 'Reservat',
          component: ()=>import("../pages/ServiceCenter/reservat.vue"),
          meta:{
            name:'预约订餐',
            requireAuth: true,
          },
        },
        {
          path: '/callus',
          name: 'CallUs',
          component: ()=>import("../pages/ServiceCenter/CallUs.vue"),
          meta:{
            name:'致电我们',
            requireAuth: true,
          },
        },
        {
          path: '/joinus',
          name: 'JoinUs',
          component: ()=>import("../pages/ServiceCenter/JoinUs.vue"),
          meta:{
            name:'加入我们',
            requireAuth: true,
          },
        }
      ]
    },

    // router.beforeEach((to, from, next) => {
    //   // console.log(to);
    //   // console.log(from);
    //   // console.log(next);
    
    //   if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
    //       if (store.state.token) {  // 通过vuex state获取当前的token是否存在
    //           next();
    //       }
    //       else {
    //           next({
    //               path: '/login',
    //               query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
    //           })
    //       }
    //   }
    //   else {
    //       next();
    //   }
    // })
  ]
})





